		mask, uniCalendar
	},
	data() {
		return {
			staticURL: this.$url.staticURL,
			uploadURL: this.$url.uploadURL,
			flag: false,
			startData:'',                  //开始日期
			endData:'',                    //结束日期
			startTime:'',                  //营业开始时间
			endTime:'',                    //营业结束日期
			time: '',                      //所选时间
			day: '',                       //所选日期
			userInfo: {
					id: '1',
