					tag: '鲁菜、粤菜、川菜、家常菜',
					grade: 9.9, //评分
					sales: 550, //销量
					evaluate: 15,//评价数
				},
			list:[{
						pic: 'test/goods01.png',
						name: '4人聚餐套餐',
						desc: '剩余数量',
						price: 1680,
						num: 6
					},
					{
						pic: 'test/goods01.png',
						name: '樱花精致日料套餐',
						desc: '剩余数量',
						price: 1960,
						num: 1
					},
					{
